___CSS_LOADER_EXPORT___.push([module.id, ".row-centered{text-align:center}.col-centered{display:inline-block;float:none;margin-right:-4px;text-align:left}.pos{position:top;top:240px}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Employees/Email/Index.vue"],"names":[],"mappings":"AAuPA,cACI,iBACJ,CAEA,cACI,oBAAqB,CACrB,UAAW,CAEX,iBAAkB,CADlB,eAEJ,CAEA,KACI,YAAa,CACb,SACJ","sourcesContent":["<template>\r\n\r\n    <Head>\r\n        <title>Home</title>\r\n    </Head>\r\n\r\n    <!--<p style=\"text-align: justify;\">Sed ut perspiciatis, unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo. Nemo enim ipsam voluptatem, quia voluptas sit, aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos, qui ratione voluptatem sequi nesciunt, neque porro quisquam est, qui dolorem ipsum, quia dolor sit amet consectetur.\r\n    </p>-->\r\n    <div class=\"row gap-20 masonry pos-r\">\r\n        <div class=\"peers fxw-nw jc-sb ai-c\">\r\n            <h3>Password Reset Log</h3>\r\n            <div class=\"peers\">\r\n                <div class=\"peer mR-10\">\r\n                    <input v-model=\"search\" type=\"text\" class=\"form-control form-control-sm\" placeholder=\"Search...\">\r\n                </div>\r\n\r\n                <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"showFilter()\">Filter</button>\r\n            </div>\r\n\r\n        </div>\r\n        <filtering v-if=\"filter\" @closeFilter=\"filter = false\">\r\n            <!-- <label>Sample Inputs</label>\r\n            <input type=\"text\" class=\"form-control\">\r\n            <button class=\"btn btn-sm btn-primary mT-5 text-white\" @click=\"\">Filter</button> -->\r\n            <!-- <div class=\"peer\">\r\n                <input type=\"checkbox\" v-model=\"typeChecked\" @change=\"filterByType\"> Filter by Type\r\n            </div> -->\r\n            Filter by type\r\n            <select v-model=\"type\" class=\"form-select\" @change=\"filterData\">\r\n                <option value=\"reset\">Password reset request\r\n                </option>\r\n                <option value=\"changed\">Changed by employee</option>\r\n            </select>\r\n            Date from\r\n            <input v-model=\"date_from\" type=\"date\" class=\"form-control\" @change=\"filterData\" />\r\n            Date to\r\n            <input v-model=\"date_to\" type=\"date\" class=\"form-control\" @change=\"filterData\" />\r\n            <br>\r\n            <button class=\"btn btn-danger btn-sm mL-2 text-white\" @click=\"clearFilters()\">Clear Filters</button>\r\n        </filtering>\r\n        <div class=\"masonry-sizer col-md-6\"></div>\r\n        <div class=\"masonry-item w-100\">\r\n            <div class=\"row gap-20\"></div>\r\n            <div class=\"bgc-white p-20 bd\">\r\n                <div class=\"table-responsive\">\r\n                    <table class=\"table table-sm table-borderless table-striped table-hover\">\r\n                        <thead>\r\n                            <tr class=\"bg-secondary text-white\">\r\n                                <th>Employee Name</th>\r\n                                <th>Changed/Reset by</th>\r\n                                <th>Requested by</th>\r\n                                <th>Date Acted</th>\r\n                                <th>Type</th>\r\n                                <th>Actions</th>\r\n                            </tr>\r\n                        </thead>\r\n                        <tbody>\r\n\r\n                            <tr v-for=\"dat in data.data\">\r\n                                <td>{{ dat.emp }}</td>\r\n                                <td>{{ dat.acted_by }}</td>\r\n                                <td>{{ dat.requested_by }}</td>\r\n                                <td>{{ formatMonthDayYear(formatDate(dat.created_at)) }} </td>\r\n                                <td>\r\n                                    <span v-if=\"dat.emp_cats == dat.acted_cats\">Changed by employee</span>\r\n                                    <span v-else>Password reset request</span>\r\n                                </td>\r\n                                <!-- <td>{{ dat.possible_risk }}</td>\r\n                                <td>{{ dat.person_affected }}</td>\r\n                                <td>{{ dat.management }}</td> -->\r\n                                <td>\r\n                                    <div class=\"dropdown dropstart\">\r\n                                        <button class=\"btn btn-secondary btn-sm action-btn\" type=\"button\"\r\n                                            id=\"dropdownMenuButton1\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\r\n                                            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\"\r\n                                                fill=\"currentColor\" class=\"bi bi-three-dots\" viewBox=\"0 0 16 16\">\r\n                                                <path\r\n                                                    d=\"M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z\" />\r\n                                            </svg>\r\n                                        </button>\r\n                                        <ul class=\"dropdown-menu action-dropdown\" aria-labelledby=\"dropdownMenuButton1\">\r\n                                            <li>\r\n                                                <!-- <Link class=\"dropdown-item\" :href=\"`/RiskManagement/${dat.id}/edit`\"> -->\r\n                                                <!-- Edit</Link> -->\r\n                                            </li>\r\n                                            <li>\r\n                                                <!-- <Link class=\"text-danger dropdown-item\"\r\n                                                    @click=\"deleteRiskManagement(dat.id)\">Delete</Link> -->\r\n                                            </li>\r\n                                        </ul>\r\n                                    </div>\r\n                                </td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n                <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <pagination :next=\"data.next_page_url\" :prev=\"data.prev_page_url\" />\r\n                    </div>\r\n                </div>\r\n                <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <p>\r\n                            {{ data.from }} to {{ data.to }} of\r\n                            {{ data.total }} entries\r\n                        </p>\r\n                    </div>\r\n                </div>\r\n\r\n            </div>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n<script>\r\nimport Filtering from \"@/Shared/Filter\";\r\nimport Pagination from \"@/Shared/Pagination\";\r\nexport default {\r\n    props: {\r\n        data: Object,\r\n        revs: Object,\r\n        revid: String,\r\n        filters: Object,\r\n    },\r\n    data() {\r\n        return {\r\n            filter: false,\r\n            date_from: \"\",\r\n            date_to: \"\",\r\n            type: \"\",\r\n            search: this.$props.filters.search,\r\n        }\r\n    },\r\n    components: {\r\n        Pagination, Filtering,\r\n    },\r\n    watch: {\r\n        search: _.debounce(function (value) {\r\n            this.$inertia.get(\r\n                \"/password/change/log\",\r\n                {\r\n                    date_from: this.date_from,\r\n                    date_to: this.date_to,\r\n                    type: this.type,\r\n                    search: value\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        }, 300),\r\n    },\r\n    methods: {\r\n\r\n        showCreate() {\r\n            this.$inertia.get(\r\n                \"/targets/create\",\r\n                {\r\n                    raao_id: this.raao_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        deleteRiskManagement(id) {\r\n            let text = \"WARNING!\\nAre you sure you want to delete the Risk Management?\" + id;\r\n            if (confirm(text) == true) {\r\n                this.$inertia.delete(\"/RiskManagement/\" + id);\r\n            }\r\n        },\r\n        getAccomplishment(tar_id) {\r\n            this.$inertia.get(\r\n                \"/accomplishments\",\r\n                {\r\n                    idtarget: tar_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        getPercent(accomp, targqty) {\r\n            var accSum = 0;\r\n            accomp.forEach(myFunction);\r\n            function myFunction(item) {\r\n                accSum += parseFloat(item.accomplishment_qty)\r\n\r\n            }\r\n            var percentt = (accSum / targqty) * 100\r\n            percentt = this.format_number(percentt, 2, true)\r\n            return percentt;\r\n        },\r\n        formatDate(dattt) {\r\n            // let dateStr = '2024-06-06T00:57:45.000000Z';\r\n\r\n            // Parse the date string into a Date object\r\n            let date = new Date(dattt);\r\n\r\n            // Get the date part in 'YYYY-MM-DD' format\r\n            return date.toISOString().split('T')[0];\r\n        },\r\n        showFilter() {\r\n            this.filter = !this.filter;\r\n        },\r\n        filterByType() {\r\n            alert('alerta')\r\n        },\r\n        async filterData() {\r\n\r\n            this.$inertia.get(\r\n                \"/password/change/log\",\r\n                {\r\n                    date_from: this.date_from,\r\n                    date_to: this.date_to,\r\n                    type: this.type,\r\n                    search: this.search\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        clearFilters() {\r\n            this.date_from = \"\";\r\n            this.date_to = \"\";\r\n            this.type = \"\";\r\n            this.filterData();\r\n        },\r\n        timePlusTwo(my_val) {\r\n            const originalDate = new Date(my_val);\r\n            originalDate.setHours(originalDate.getHours() + 2);\r\n            return originalDate.toISOString();\r\n        }\r\n    }\r\n};\r\n</script>\r\n<style>\r\n.row-centered {\r\n    text-align: center;\r\n}\r\n\r\n.col-centered {\r\n    display: inline-block;\r\n    float: none;\r\n    text-align: left;\r\n    margin-right: -4px;\r\n}\r\n\r\n.pos {\r\n    position: top;\r\n    top: 240px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
